@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap);
:root {
    --nunito: 'Nunito', sans-serif;

    --black: #0D1821;
    --white: #FEF9EF;
    --blue: #4C6085;
    --green: #69A197;
    --yellow: #FFCB77;
    --coral: #FE6D73; 
}
#logo {
    margin-top: 2rem;
    margin-bottom: 1rem;
}

#logo img {
    height: 64px;
}

@media (max-width: 1200px) {
    #sidebar-contents {
        height: 5rem;
        width: 85%;
        margin: auto;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }

    #logo {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        grid-gap: 1rem;
        gap: 1rem;
        margin: 0;
    }

    #logo img {
        height: 40px;
    }

    #logo h3 {
        display: none;
    }

    #account-details {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        grid-gap: 1rem;
        gap: 1rem;
    }
}

@media (max-width: 630px) {
    #sidebar-contents {
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: center;
                justify-content: center;
        margin: 1rem auto;
    }

    #logo {
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    #logo img {
        height: 32px;
        margin-bottom: -1rem;
    }
}
#new-note {
    display: -webkit-flex;
    display: flex;
    width: 50%;
    margin-left: 3rem;
    padding: 1rem;
}

#new-note > #note {
    margin-right: 0.4rem;
}

#new-note > #note label {
    display: none;
}

#new-note > #note textarea {
    width: 600px;
    min-width: 600px;
    max-width: 600px;
    padding: 1rem;
    min-height: 100px;
    font-family: var(--nunito);
}

#new-note > .actions {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

button {
    padding: 10px 30px;
    margin-top: 0.2rem;
    margin-left: 0.2rem;
    font-size: 1.2rem;
    background: var(--coral);
    border: 1px solid var(--coral);
    color: var(--black);
}

button:hover {
    cursor: pointer;
    background: var(--black);
    border: 1px solid var(--black);
    color: var(--yellow);
}

#note-list {
    width: 100%;
    margin-bottom: 7rem;
}

.note {
    width: 66%;
    max-width: 600px;
    text-align: left;
    padding: 1rem;
    border: 1px solid var(--black);
    margin-left: 4rem;
    margin-top: 1rem;
    position: relative;
}

.note > .text {
    z-index: 0;
}

.note > .actions {
    display: none;
    position: absolute;
    right: 0.4rem;
    bottom: 0.4rem;
    z-index: 1;
}

.note:hover > .actions {
    display: block;
}

.note button {
    cursor: pointer;
    font-size: 1rem;
    padding: 0.6rem;
}

@media (max-width: 1200px) {
    #new-note {
        -webkit-flex-direction: column;
                flex-direction: column;
        margin: auto;
        -webkit-align-items: center;
                align-items: center;
        text-align: center;
        max-width: 800px;
    }

    #new-note > #note {
        margin-bottom: 0.4rem;
    }

    #new-note > #note textarea {
        width: 66%;
        min-width: 500px;
        max-width: 800px;
    }

    #new-note > .actions {
        -webkit-flex-direction: row;
                flex-direction: row;
    }
    
    .note {
        width: 90%;
        max-width: 800px;
        margin: 1rem auto;
    }
}

@media (max-width: 630px) {
    #new-note > #note textarea {
        min-width: 350px;
        max-width: 500px;
    }
}
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--nunito);
  color: var(--black);
  background-color: var(--white);
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}


#sidebar {
  width: 20%;
  position: fixed;
  padding: 1rem;
  text-align: center;
  background: var(--coral);
  color: var(--black);
  height: 100vh;
}

#main {
  width: 80%;
  margin-left: 20%;
  margin-top: 2rem;
}

@media (max-width: 1200px) {
  .App {
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-align-items: center;
              align-items: center;
  }

  #sidebar {
      width: 100%;
      height: 5rem;
  }

  #main {
      width: 90%;
      margin: 8rem auto;
      -webkit-align-items: center;
              align-items: center;
  }
}

@media (max-width: 630px) {
  #sidebar {
      height: 7rem;
  }

  #main {
      margin: 10rem auto;
  }
}
